.font {
  font-variant: tabular-nums;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 14px;
  color: #000;
}
.block,
.leftmenu .item {
  background: #fff;
  box-shadow: 0 8px 10px rgb(49 70 17.8%);
  border-radius: 8px;
}
.leftmenu {
  width: 100%;
  height: 100%;
}
.leftmenu .item {
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.leftmenu .item:nth-child(2) {
  margin-top: 12px;
}
.leftmenu .item .list {
  flex: 1 0 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 12px;
}
.leftmenu .item .list .countryContainer {
  flex: 1;
  width: 50%;
  min-width: 50%;
  max-width: 50%;
  display: flex;
}
.leftmenu .item .list .countryContainer .countryItem {
  cursor: pointer;
  text-align: center;
  width: 100%;
  height: 36px;
  padding: 0 3px;
  font-size: 14px;
  white-space: pre;
  margin: 12px 2px;
  background: #fafafa;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  opacity: 1;
  transition: all 0.3s;
  display: inline-block;
  overflow: hidden;
}
.leftmenu .item .list .countryContainer .clicked {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
  border-color: #1890ff;
  color: #1890ff;
}
.leftmenu .itemTop {
  height: calc(50% - 55px);
}
.leftmenu .itemBottom {
  height: calc(50% + 40px);
}
.clicked {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
  border-color: #1890ff;
  color: #1890ff;
}
.btn {
  margin-top: 24px;
  font-size: 12px;
  margin-bottom: 24px;
}
.middle {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
