.map {
  display: block;
  width: 100%;
}
.colorScale {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
  width: 100px;
  height: 20px;
  border-radius: 2px;
  display: flex;
  flex-direction: row;
}
.left {
  position: relative;
}
.left::after {
  position: absolute;
  content: "-1";
  display: block;
  left: -20px;
  font-size: 12px;
  font-weight: 450;
}
.right {
  position: relative;
}
.right::after {
  position: absolute;
  content: "1";
  display: block;
  right: -15px;
  font-size: 12px;
  font-weight: 450;
}
.choropleth_container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.shadow1 {
  filter: drop-shadow(-2px -2px 10px black);
}
.shadow2 {
  filter: drop-shadow(2px 2px 10px black);
}
