.search_table {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.search_table .search_top {
  display: flex;
  height: 80px;
  padding: 16px 16px 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.search_table .search_list {
  flex: 1 0 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 0 12px;
}
.search_table .search_list .search_container {
  overflow: auto;
}
.search_table :global .ant-table-cell-fix-right {
  white-space: nowrap;
}
.tooltipText {
  margin-right: 2px;
}
.hover {
  cursor: pointer;
}
