.font {
  font-variant: tabular-nums;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 14px;
  color: #000;
}
.block,
.main_content .bottom .progress,
.main_content .bottom .container .chart_content .first .firstRight .force,
.main_content .bottom .container .chart_content .second .secondLeft .pieMapContainer,
.main_content .bottom .container .chart_content .second .secondRight .stack,
.right_menu {
  background: #fff;
  box-shadow: 0 8px 10px rgb(49 70 17.8%);
  border-radius: 8px;
}
.basic_layout {
  width: 100vw;
  height: 100vh;
  display: flex;
}
.left_menu {
  padding-top: 16px;
  padding-bottom: 12px;
  padding-left: 8px;
  flex: 1 0 0;
}
.main_content {
  flex: 6 0 0;
  display: flex;
  flex-direction: column;
  padding: 16px;
}
.main_content .bottom {
  flex: 5 0 0;
  display: flex;
  flex-direction: column;
}
.main_content .bottom .progress {
  margin-top: 12px;
  height: 80px;
  display: flex;
  flex-direction: column;
}
.main_content .bottom .progress .main {
  flex: 1 0 0;
  margin-top: 12px;
  display: flex;
  flex-direction: row;
}
.main_content .bottom .progress .main .playBtn {
  margin: 0 12px;
}
.main_content .bottom .progress .main .sortBtn {
  margin-right: 6px;
}
.main_content .bottom .progress .main .sortBtn .sorted {
  color: #40a9ff;
}
.main_content .bottom .progress .main .progressContent {
  flex: 1 0 0;
  display: flex;
  flex-direction: row;
  min-height: 0;
}
.main_content .bottom .container {
  flex: 1 0 0;
  display: flex;
  flex-direction: row;
}
.main_content .bottom .container .chart_content {
  flex: 6 0 0;
  display: flex;
  flex-direction: column;
}
.main_content .bottom .container .chart_content .first {
  display: flex;
  flex-direction: row;
  flex: 1 0 0;
}
.main_content .bottom .container .chart_content .first .firstLeft {
  flex: 3 0 0;
  height: 100%;
}
.main_content .bottom .container .chart_content .first .firstLeft .topMap {
  flex: 1 0 0;
  height: 100%;
  display: flex;
}
.main_content .bottom .container .chart_content .first .firstRight {
  flex: 1 0 0;
  height: 100%;
  margin-left: 12px;
}
.main_content .bottom .container .chart_content .first .firstRight .force {
  flex: 1 0 0;
  height: 100%;
  display: flex;
}
.main_content .bottom .container .chart_content .second {
  flex: 1 0 0;
  margin-top: 12px;
  display: flex;
  flex-direction: row;
}
.main_content .bottom .container .chart_content .second .secondLeft {
  flex: 3 0 0;
  display: flex;
}
.main_content .bottom .container .chart_content .second .secondLeft .pieMapContainer {
  flex: 1 0 0;
  display: flex;
}
.main_content .bottom .container .chart_content .second .secondRight {
  flex: 1 0 0;
  display: flex;
  margin-left: 12px;
}
.main_content .bottom .container .chart_content .second .secondRight .stack {
  flex: 1 0 0;
  display: flex;
}
.main_content .bottom .container .radar_chart {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  background-color: #fff;
  border-radius: 8px;
}
.right_menu {
  margin: 16px 0;
  flex: 2 0 0;
  overflow: auto;
}
