.line {
  stroke: #858570;
  stroke-opacity: 0.7;
  stroke-width: 1;
  fill: none;
  stroke-linecap: round;
}
.g_grid .grid {
  stroke: #e9e9e9;
  stroke-opacity: 0.9;
  stroke-width: 1;
  fill: none;
  stroke-linecap: round;
}
.g_grid .grid:first-child {
  stroke: #434456;
  stroke-width: 2;
  fill: #eeeeee;
  fill-opacity: 0.6;
}
