.radar_container {
  display: flex;
  flex-direction: column;
  margin: 10px;
  transition: "all .5s ease";
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 2px -2px, rgba(0, 0, 0, 0.12) 0px 3px 6px, rgba(0, 0, 0, 0.09) 0px 5px 12px 4px;
  padding-top: 15px;
  border-radius: 5px;
}
.radar_container .radar_chart {
  flex: 0 0 80%;
  position: relative;
  display: flex;
  justify-content: center;
}
.radar_container .radar_title {
  flex: 0 0 20%;
  font-size: 1em;
  font-weight: 500;
  text-align: center;
  text-anchor: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 0 10px;
}
.radar_container.display_list {
  position: relative;
  cursor: pointer;
}
.radar_container.display_list .close {
  position: absolute;
  right: 10px;
  top: 5px;
}
.display_choose.enter {
  border: dashed;
  border-radius: 10px;
}
