.pieMap {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
}
.pieMap .maps {
  flex: 1 0 0;
  display: flex;
  flex-direction: row;
}
.pieMap .maps .select {
  width: 140px;
  margin-bottom: 12px;
}
.pieMap .maps .basicMap {
  display: flex;
  flex-direction: column;
}
.pieMap .maps .sourceMap {
  flex: 25%;
  min-width: 25%;
  max-width: 25%;
  display: flex;
  flex-direction: column;
}
.pieMap .maps .middleMap {
  flex: 50%;
  min-width: 50%;
  max-width: 50%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.pieMap .maps .targetMap {
  flex: 25%;
  min-width: 25%;
  max-width: 25%;
  display: flex;
  flex-direction: column;
}
.chart {
  flex: 2;
}
.placeholder {
  flex: 1;
}
.sourceCountryMap {
  display: flex;
  justify-content: center;
  height: 80%;
}
.sourceCountryMap svg {
  stroke: #fff;
  height: 100%;
}
.sourceCountryMap svg path {
  fill: #a82b2b;
  cursor: pointer;
  outline: none;
}
.sourceCountryMap svg path:hover {
  fill: rgba(168, 43, 43, 0.83);
}
.sourceCountryMap svg path:focus {
  fill: rgba(168, 43, 43, 0.6);
}
.targetCountryMap {
  display: flex;
  justify-content: center;
  height: 80%;
}
.targetCountryMap svg {
  stroke: #fff;
  height: 100%;
}
.targetCountryMap svg path {
  fill: #317ebd;
  cursor: pointer;
  outline: none;
}
.targetCountryMap svg path:hover {
  fill: #1c70b4;
}
.targetCountryMap svg path:focus {
  fill: #1874c0;
}
.spin {
  height: 100%;
}
.spin :global .ant-spin-container {
  height: 100%;
}
