.tooltip {
  transform: translate(-50%, 0px);
  position: fixed;
  background-color: #a7aeb5;
  padding: 5px 10px;
  text-align: center;
  color: white;
  pointer-events: none;
  z-index: 10000;
}
.tooltip::after {
  content: "";
  position: absolute;
  width: 0;
  transform: translate(-50%, 0px);
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #a7aeb5;
  border-bottom: 10px solid transparent;
}
