.links {
  stroke-opacity: 0.6;
}
.nodes {
  stroke-width: 1.5;
}
.link {
  color: red;
}
.node {
  color: red;
}
.legends {
  height: 50px;
  padding: 5px;
  overflow: auto;
}
.forceGraph {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.forceGraph .content {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
}
.forceGraph .content .spin {
  height: 100%;
}
.forceGraph .content .spin :global .ant-spin-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
