.search_dropdown {
  position: relative;
  display: inline-block;
}
.search_dropdown input {
  padding: 0;
  border: none;
  border-radius: 0;
  box-shadow: inset 0 -1px 0 #ddd;
  font-size: 16px;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  line-height: 1.846;
  color: #666666;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 0.1px;
  text-rendering: optimizeLegibility;
}
.search_dropdown input:focus {
  -webkit-box-shadow: inset 0 -2px 0 #2196f3;
  box-shadow: inset 0 -2px 0 #2196f3;
  outline: 0;
}
.search_dropdown .div_arrow {
  position: absolute;
  right: 5px;
  top: 0px;
  padding-top: 5px;
  vertical-align: middle;
  padding-left: 3px;
  width: 10px;
}
.search_dropdown .div_arrow .hide_arrow {
  display: inline-block;
  opacity: 0;
  transition: opacity 0.15s linear 0s, transform 0.15s linear 0s;
  transform: translateY(5px);
}
.search_dropdown .div_arrow .show_arrow {
  display: inline-block;
  opacity: 1;
  transition: opacity 0.15s linear 0s, transform 0.15s linear 0s;
  transform: translateY(0px);
}
.search_dropdown .dropdown {
  z-index: 100;
  width: 180px;
  display: flex;
  position: absolute;
  left: 0px;
  top: 100%;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: rgba(100, 100, 100, 0.3) 1px 1px 4px;
  overflow-y: auto;
  max-height: 15rem;
}
.search_dropdown .dropdown .option {
  padding: 1rem 1.5rem;
  cursor: pointer;
  color: #4d4d4c;
  background-color: #ffffff;
}
.search_dropdown .dropdown .hightlight {
  color: #ffffff;
  background-color: #4271ae;
}
.search_area {
  display: flex;
  justify-content: center;
  padding: 5px 0;
}
