.font {
  font-variant: tabular-nums;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 14px;
  color: #000;
}
.block,
.container {
  background: #fff;
  box-shadow: 0 8px 10px rgb(49 70 17.8%);
  border-radius: 8px;
}
.topmap {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}
.content {
  margin-top: 12px;
  flex: 1 0 0;
  position: relative;
}
.spin {
  height: 100%;
}
.spin :global .ant-spin-container {
  height: 100%;
  display: flex;
}
.svg {
  pointer-events: none;
  position: relative;
  z-index: 1;
}
.svg path {
  fill: none;
  stroke-width: 2px;
}
.container {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.left {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
}
.left .map {
  flex: 1 0 0;
  position: relative;
}
.left .map .circle {
  position: absolute;
  width: 4px;
  height: 4px;
  background-color: #7f7f7f;
  border-radius: 50%;
}
.middle {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  transform: translateX(-75px);
  position: relative;
}
.middle::after {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-left: 1px dashed #d5d5d5;
  border-right: 1px dashed #d5d5d5;
}
.right {
  flex: 1 0 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 80px;
}
.countryMap {
  height: 100%;
}
.colorScale {
  position: absolute;
  border-radius: 2px;
  bottom: 15px;
  right: 15px;
  width: 25px;
  height: 100px;
}
